  :deep(.p-datatable .p-datatable-tbody > tr > td) {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    min-width: auto;
    max-width: 12rem;
  }
  :deep(.p-datatable .p-datatable-tbody > .p-datatable-row-expansion > td) {
    padding: 0px;
    margin: 0px;
  }
