</template>

<script setup lang="ts">
  import { RouterLink } from "vue-router"
  import { ref, onBeforeMount, onUnmounted, watch } from "vue"

  const show = ref<boolean>(true)
  const open = ref<boolean>(false)
  const offset = ref<number>(0)

  onBeforeMount(() => window.addEventListener("scroll", handleScroll))
  onUnmounted(() => window.removeEventListener("scroll", handleScroll))

  const handleScroll = () => (offset.value = window.pageYOffset)
  watch(offset, (oldVal, newVal) => {
    show.value = newVal - oldVal > 0
    open.value = false
  })

  const toggleMenu = () => (open.value = !open.value)
</script>

<style scoped>
  .v-enter-active,
  .v-leave-active {
    transition: opacity 0.5s ease;
  }
  .v-enter-from,
  .v-leave-to {
    opacity: 0;
